import React from "react";
import SidebarItem from "./SidebarItem";
import UserProfile from "./UserProfile";

const Sidebar: React.FC = () => {
  return (
    <aside
      className="w-64 h-screen flex flex-col justify-between p-4 bg-background-2"
      style={{ backgroundColor: "var(--background-2)" }}
    >
      {/* Espacio para el logo */}
      <div className="mb-8 bg-gray-300 h-16 rounded"></div>

      {/* Items del menú */}
      <nav className="flex-1">
        <SidebarItem
          icon="/icons/progress.svg"
          label="Tu Progreso"
          href="/progreso"
        />
        <SidebarItem
          icon="/icons/resources.svg"
          label="Recursos Adicionales"
          href="/recursos"
        />
        <SidebarItem
          icon="/icons/certifications.svg"
          label="Certificaciones"
          href="/certificaciones"
        />
        <SidebarItem
          icon="/icons/support.svg"
          label="Soporte y Ayuda"
          href="/soporte"
        />
      </nav>

      {/* Perfil del usuario */}
      <UserProfile
        name="Camila Gómez"
        role="Estudiante"
        avatar="/images/avatar.jpg"
      />
    </aside>
  );
};

export default Sidebar;
